//REACT
import React from "react";

//BOOTSTRAP
import {Row, Col, Button } from "react-bootstrap";

//Image

import ScrollAnimation from "react-animate-on-scroll";


const SharedComponent = (props) => {
  const scrollTop = () => {
    window.scrollTo({ top: 0, left: 0, behavior: "smooth" });
  };
  return (
    <Row className="py-5 sharedComponent">
      <Col md={1}></Col>
      <Col md={5} className="py-3 order-md-0 order-1">
        <ScrollAnimation animateIn="jello">
          <h1>Get started with us today</h1>
          <p className="py-3">
            Here you can explain why people should definitely use our platform.
          </p>
        </ScrollAnimation>
        <ScrollAnimation animateIn="bounceInLeft">
        <button onClick={scrollTop}>
          <Button className="btn-signUp" as={props.link} to={props.to} onClick={props.click}>Sign up now</Button>
          </button>
        </ScrollAnimation>
      </Col>

      <Col md={5} className="py-3 order-md-1 order-0">
        <ScrollAnimation animateIn="tada">
          <img src={props.img} alt="pic2" className="rounded"/>
        </ScrollAnimation>
      </Col>
    </Row>
  );
};

export default SharedComponent;
