import React from "react";
import { useForm } from "react-hook-form";

const ContactForm = () => {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();
  const handleRegistration = (data) => console.log(data);

  const registerOptions = {
    name: { required: "Name is required" },
    email: { required: "Email is required" },
    userMessage: {required: "Please enter your message!"}
  };

  return (
    <form onSubmit={handleSubmit(handleRegistration)}>
      <div>
        <h1>
          <b>Contact us</b>
        </h1>
        <p className="text-muted">
          Explore the future with us. <br></br> Feel free to get in touch.
        </p>
        <label>
         <b> Name </b><span className="text-danger">*</span>
        </label>
        <br></br>
        <input
          className="form-control"
          type="text"
          name="name"
          placeholder="Full Name"
          {...register("name", registerOptions.name, { required: true })}
        />
        <div className="error text-danger">
          {errors?.name && errors.name.message}
        </div>

        <label>
          <b> Email </b>
          <span className="text-danger">*</span>
        </label>

        <input
          className="form-control"
          type="email"
          name="email"
          placeholder="Email address"
          {...register("email", registerOptions.email, { required: true })}
        />
        <div className="error text-danger">
          {errors?.email && errors.email.message}
        </div>

        <label>
          <b>Message</b> <span className="text-danger">*</span>
        </label>
        <br></br>
        <textarea
          name="userMessage"
          className="form-control"
          placeholder="How can we help you?"
          {...register("userMessage", registerOptions.userMessage, {
            required: true,
          })}
        ></textarea>
        <div className="error text-danger">
          {errors?.userMessage && errors.userMessage.message}
        </div>
        <br></br>
      </div>
      <button className="btn btn-send">Send Message</button>
    </form>
  );
};
export default ContactForm;
